import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../commons/WebComponentWrapper";
import { JSONEditor } from "@json-editor/json-editor";
import { EventAPI } from "../../commons/EventAPI";

const dispatchedEvents = [];

/*
    Example: <srs-jsoneditor schema={{...}}/>
*/
const SrsJSONEditor = function (props, ref) {
    const editorRef = useRef(null);

    useEffect(() => {
        const config = {
            disable_edit_json: true,
            disable_properties: true,
            disable_array_reorder: true,
            disable_array_delete_last_row: true,
            schema: props.schema || {}
        };

        const editor = new JSONEditor(editorRef.current, config);
        editor.on('change', function () {
            let value = JSON.stringify(editor.getValue());
            console.log(value);
            props.dispatchEvent(EventAPI.JSONEDITOR_CHANGED, { payload: {id: props.id || props.elementid, value}});
        });
        return () => {
            editor.destroy();
        };
    }, []);

    return <div ref={editorRef}></div>;
}

